import useLinks from '../../../../hooks/links/useLinks';
import useSiteData from '../../../../hooks/siteData/useSiteData';
import Facebook from '../../Icons/Facebook/Facebook';
import Globe from '../../Icons/Globe/Globe';
import Instagram from '../../Icons/Instagram/Instagram';
import Linkedin from '../../Icons/Linkedin/Linkedin';
import Person from '../../Icons/Person/Person';
import Tiktok from '../../Icons/Tiktok/Tiktok';
import Twitter from '../../Icons/Twitter/Twitter';

const Links = ({
  className,
  style,
  includeSocials,
  direction,
}: {
  className?: string;
  style?: 'secondary';
  includeSocials?: boolean;
  direction: 'col' | 'row';
}) => {
  const { data: siteData } = useSiteData();
  const { data: links } = useLinks();

  if (!links) {
    return null;
  }

  const buttonClass =
    style === 'secondary'
      ? `border-gray border-[1px] ${
          direction === 'col' ? 'py-3 px-4' : 'py-2 px-3'
        } hover:bg-gray-100 rounded`
      : `rounded border-0 bg-button ${
          direction === 'col' ? 'py-3 px-4' : 'py-2 px-3'
        } text-white hover:bg-button-hover`;

  const flexDir = direction === 'col' ? 'flex-col' : 'flex-row';

  return (
    <div className={`-m-2 flex ${flexDir} flex-wrap ${className}`}>
      {includeSocials && (
        <>
          {siteData?.social.map((social, index) => (
            <a
              href={
                social.type !== 'dotyouid'
                  ? `https://${social.type}.com/${social.username}`
                  : `https://${social.username}`
              }
              key={index}
              className={`m-2 flex flex-row focus:outline-none ${buttonClass}`}
              target="_blank"
              rel={'noopener noreferrer'}
            >
              {social.type === 'facebook' ? (
                <Facebook className="my-auto mr-5 h-4 w-4" />
              ) : social.type === 'twitter' ? (
                <Twitter className="my-auto mr-5 h-4 w-4" />
              ) : social.type === 'linkedin' ? (
                <Linkedin className="my-auto mr-5 h-4 w-4" />
              ) : social.type === 'instagram' ? (
                <Instagram className="my-auto mr-5 h-4 w-4" />
              ) : social.type === 'tiktok' ? (
                <Tiktok className="my-auto mr-5 h-4 w-4" />
              ) : social.type === 'dotyouid' ? (
                <Person className="my-auto mr-5 h-4 w-4" />
              ) : (
                <Globe className="my-auto mr-5 h-4 w-4" />
              )}
              {social.username}
            </a>
          ))}
        </>
      )}
      {links.map((link) => (
        <a
          href={link.target}
          key={link.target}
          className={`m-2 flex flex-row focus:outline-none ${buttonClass}`}
          target="_blank"
          rel={'noopener noreferrer'}
        >
          <Globe className="my-auto mr-5 h-4 w-4" />
          {link.text}
        </a>
      ))}
    </div>
  );
};

export default Links;
