import { useQuery } from '@tanstack/react-query';
import {
  AttributeDefinitions,
  BuiltInProfiles,
  PublicClient,
  Attribute,
  LinkFields,
} from '@youfoundation/dotyoucore-js';
import useAuth from '../auth/useAuth';

const useLinks = () => {
  const { getSharedSecret, getApiType } = useAuth();

  const fetchData: () => Promise<
    { text: string; target: string; id: string }[] | undefined
  > = async () => {
    const client = new PublicClient({ api: getApiType(), sharedSecret: getSharedSecret() });

    const fileData = await client.fileReadOnlyProvider.GetFile('sitedata.json');
    if (fileData.has('link')) {
      const linkAttributes = fileData
        .get('link')
        ?.sort((attrA, attrB) => attrB.payload.priority - attrA.payload.priority)
        .map((entry) => {
          const attribute = entry.payload as Attribute;

          return {
            text: attribute.data[LinkFields.LinkText] as string,
            target: attribute.data[LinkFields.LinkTarget] as string,
            id: attribute.id,
          };
        });
      if (linkAttributes?.length) {
        return linkAttributes;
      }
    }

    const linkAttributes = await client.profileDataProvider.getAttributeVersions(
      BuiltInProfiles.StandardProfileId,
      undefined,
      [AttributeDefinitions.Link.type]
    );

    return linkAttributes
      ?.sort((attrA, attrB) => attrB.priority - attrA.priority)
      .map((attribute) => {
        return {
          text: attribute.data[LinkFields.LinkText] as string,
          target: attribute.data[LinkFields.LinkTarget] as string,
          id: attribute.id,
        };
      });
  };

  return useQuery(['link'], fetchData, {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
    staleTime: Infinity,
  });
};

export default useLinks;
